// Bootstrap functions
//
// Utility mixins and functions for evaluating source code across our variables, maps, and mixins.

// Ascending
// Used to evaluate Sass maps like our grid breakpoints.
@mixin _assert-ascending($map, $map-name) {
  $prev-key: null;
  $prev-num: null;
  @each $key, $num in $map {
    @if $prev-num == null or unit($num) == "%" or unit($prev-num) == "%" {
      // Do nothing
    } @else if not comparable($prev-num, $num) {
      @warn "Potentially invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} whose unit makes it incomparable to #{$prev-num}, the value of the previous key '#{$prev-key}' !";
    } @else if $prev-num >= $num {
      @warn "Invalid value for #{$map-name}: This map must be in ascending order, but key '#{$key}' has value #{$num} which isn't greater than #{$prev-num}, the value of the previous key '#{$prev-key}' !";
    }
    $prev-key: $key;
    $prev-num: $num;
  }
}

// Starts at zero
// Used to ensure the min-width of the lowest breakpoint starts at 0.
@mixin _assert-starts-at-zero($map, $map-name: "$grid-breakpoints") {
  $values: map-values($map);
  $first-value: nth($values, 1);
  @if $first-value != 0 {
    @warn "First breakpoint in #{$map-name} must start at 0, but starts at #{$first-value}.";
  }
}

// Replace `$search` with `$replace` in `$string`
// Used on our SVG icon backgrounds for custom forms.
//
// @author Hugo Giraudel
// @param {String} $string - Initial string
// @param {String} $search - Substring to replace
// @param {String} $replace ('') - New value
// @return {String} - Updated string
@function str-replace($string, $search, $replace: "") {
  $index: str-index($string, $search);

  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
  }

  @return $string;
}

// Color contrast
@function color-yiq($color, $dark: $yiq-text-dark, $light: $yiq-text-light) {
  $r: red($color);
  $g: green($color);
  $b: blue($color);

  $yiq: (($r * 299) + ($g * 587) + ($b * 114)) / 1000;

  @if ($yiq >= $yiq-contrasted-threshold) {
    @return $dark;
  } @else {
    @return $light;
  }
}

// Retrieve color Sass maps
@function color($key: "blue") {
  @return map-get($colors, $key);
}

@function theme-color($key: "primary") {
  @return map-get($theme-colors, $key);
}

@function gray($key: "100") {
  @return map-get($grays, $key);
}

// Request a theme color level
@function theme-color-level($color-name: "primary", $level: 0) {
  $color: theme-color($color-name);
  $color-base: if($level > 0, $black, $white);
  $level: abs($level);

  @return try-mix($color-base, $color, $level * $theme-color-interval);
}

@function try-darken($color, $percent) {

  @if (not $enable-variables){
    @return darken($color, $percent);
  }

  @each $themecolor, $value in $theme-colors {
    @if $value == $color {
      $percent-str: "#{$percent * 100}";
      $percent-value: str_slice($percent-str, 0, str_length($percent-str) - 1);
      $variable: --#{$themecolor}-darken-#{$percent-value};
      @return var($variable);
    }
  }
  @return darken($color, $percent);
}

@function try-lighten($color, $percent) {

  @if (not $enable-variables){
    @return lighten($color, $percent);
  }

  @each $themecolor, $value in $theme-colors {
    @if $value == $color {
      $percent-str: "#{$percent * 100}";
      $percent-value: str_slice($percent-str, 0, str_length($percent-str) - 1);
      $variable: --#{$themecolor}-lighten-#{$percent-value};
      @return var($variable);
    }
  }
  @return lighten($color, $percent);
}


@function try-color-yiq($color, $dark: $yiq-text-dark, $light: $yiq-text-light)
{
  @if (not $enable-variables){
    @return color-yiq($color, $dark, $light);
  }

  @if str_slice("#{$color}", 0, 3) == "var" {
    $original: str_slice($color, 7, str_length($color) - 1);
    $yiq: --#{$original}-yiq;
    @return var($yiq);
  }

  @return color-yiq($color, $dark, $light);
}


@function try-mix($color-base, $color, $level){

  @if (not $enable-variables){
    @return mix($color-base, $color, $level);
  }

  @if (str_slice("#{$color}", 0, 3) == "var" or str_slice("#{$color-base}", 0, 3) == "var") {
    @return $color-base; //TODO
  }

  @return mix($color-base, $color, $level);
}
